import React from 'react'
import { makeStyles, withStyles, Theme, createStyles } from '@material-ui/core/styles'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Typography from '@material-ui/core/Typography'
import { useHistory } from 'react-router-dom'
// import styles from './Navbar.module.css'

import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'

import MinNavbar from './MinNavbar/MinNavbar'

interface StyledTabsProps {
	value: number
	onChange: (event: React.ChangeEvent<{}>, newValue: number) => void
}

const StyledTabs = withStyles({
	indicator: {
		display: 'flex',
		justifyContent: 'center',
		backgroundColor: 'transparent',
		'& > span': {
			maxWidth: 40,
			width: '100%',
			backgroundColor: '#091021',
		},
	},
})((props: StyledTabsProps) => <Tabs {...props} TabIndicatorProps={{ children: <span /> }} />)

interface StyledTabProps {
	label: string
}

const StyledTab = withStyles((theme: Theme) =>
	createStyles({
		root: {
			textTransform: 'none',
			color: '#091021',
			fontWeight: theme.typography.fontWeightBold,
			fontSize: theme.typography.pxToRem(15),
			'&:focus': {
				opacity: 1,
			},
		},
	})
)((props: StyledTabProps) => <Tab disableRipple {...props} />)

const useStyles = makeStyles((theme: Theme) => ({
	root: {
		flexGrow: 1,
	},
	padding: {
		padding: theme.spacing(0),
	},
	menu: {
		display: 'flex',
		justifyContent: 'center',
		backgroundColor: '#ffffff',
		paddingTop: 20,
	},
}))

const paths = ['/', '/shop', '/suppliers', '/faq', '/reviews']

const useStylesMin = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			flexGrow: 1,
		},
		menuButton: {
			marginRight: theme.spacing(2),
			color: '#ffffff',
		},
		title: {
			flexGrow: 1,
		},
	})
)

export default function Navbar() {
	const classes = useStyles()
	const classesMin = useStylesMin()
	const history = useHistory()
	const [location] = history.location.pathname.slice(1).split('/')
	const pathIndex = paths.indexOf(`/${location}`)
	const [value, setValue] = React.useState(pathIndex > -1 ? pathIndex : 0)

	const [openMinNav, setOpenMinNav] = React.useState(false)

	const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
		setValue(newValue)
		setTimeout(() => {
			history.push(paths[newValue])
		}, 250)
	}

	if (window.visualViewport.width < 800)
		return (
			<div className={classes.root}>
				<MinNavbar openMinNav={openMinNav} setOpenMinNav={setOpenMinNav} paths={paths} />
				<AppBar position="static">
					<Toolbar style={{ background: '#4c566f' }}>
						<IconButton edge="start" className={classesMin.menuButton} onClick={() => setOpenMinNav(true)} aria-label="menu">
							<MenuIcon />
						</IconButton>
					</Toolbar>
				</AppBar>
			</div>
		)

	return (
		<div className={classes.root}>
			<div className={classes.menu}>
				<StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
					<StyledTab label="Главная" />
					<StyledTab label="Магазин" />
					<StyledTab label="Поставщикам" />
					<StyledTab label="FAQ" />
					<StyledTab label="Отзывы" />
				</StyledTabs>
				<Typography className={classes.padding} />
			</div>
		</div>
	)
}
