import React from 'react'
import styles from './Shop.module.css'
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import LocalAtmIcon from '@material-ui/icons/LocalAtm'
import { Link } from 'react-router-dom'

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			width: '100%',
			maxWidth: 800,
			backgroundColor: theme.palette.background.paper,
		},
	})
)

interface Categorys {
	retval: number
	retdesc: string
	queryId: string
	category: [
		{
			id: string
			name: string
			cnt: string
		}
	]
}

export default function CategoryesComponent() {
	const [categorys, setCategorys] = React.useState<Categorys>()
	const classes = useStyles()

	React.useEffect(() => {
		fetch('https://api.digiseller.ru/api/categories?seller_id=654077', {
			headers: {
				Accept: 'application/json',
			},
		})
			.then(response => response.json())
			.then(response => setCategorys({ ...response }))
	}, [])

	if (!categorys) return <>Загрузка категорий...</>

	return (
		<>
			<p className={styles.pathName}>Магазин</p>
			<List component="nav" className={classes.root} aria-label="contacts">
				{categorys?.category.map(cat => (
					<ListItem button key={cat.id} component={Link} to={`/shop/${cat.id}`}>
						<ListItemIcon>
							<LocalAtmIcon fontSize="large" style={{ color: '#5bbf97' }} />
						</ListItemIcon>
						<ListItemText
							primary={<span className={styles.listPrimary}>{cat.name}</span>}
							secondary={
								<span className={styles.listSecondary}>
									Количество товаров: <span className={styles.colored}>{cat.cnt}</span>
								</span>
							}
						/>
					</ListItem>
				))}
			</List>
		</>
	)
}
