import React from 'react'
import { Switch, Route } from 'react-router-dom'
import CategoryesComponent from './CategoryesComponent'
import ProductsRoute from './ProductsRoute'

export default function CategoryesRoute() {
	return (
		<Switch>
			<Route exact path="/shop" component={CategoryesComponent} />
			<Route path="/shop/:category" component={ProductsRoute} />
		</Switch>
	)
}
