import React from 'react'
import styles from './Shop.module.css'
import { RouteComponentProps } from 'react-router-dom'
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import LocalAtmIcon from '@material-ui/icons/LocalAtm'
import { Link } from 'react-router-dom'

interface Params {
	category: string
}

interface Products {
	retval: string
	retdesc: string
	lang: string
	queryId: string
	totalPages: string
	totalItems: string
	breadCrumbs: [
		{
			id: string
			name: string
		}
	]
	categories: []
	product: Product[]
}

interface Product {
	id: string
	name: string
	cntImg: string
	info: string
	price: string
	base_price: string
	base_currency: string
	currency: string
	price_rub: string
	price_usd: string
	price_eur: string
	price_uah: string
	partner_comiss: string
	agency_id: string
	collection: string
	is_available: number
	has_discount: number
	id_present: number
	sale_info: {
		common_base_price: string
		common_price_usd: string
		common_price_rur: string
		common_price_eur: string
		common_price_uah: string
		sale_end: string
		sale_percent: string
	}
	label: string
}

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			width: '100%',
			maxWidth: 800,
			backgroundColor: theme.palette.background.paper,
		},
	})
)

export default function ProductsComponent({ match }: RouteComponentProps<Params>) {
	const [products, setProducts] = React.useState<Products>()
	const classes = useStyles()

	React.useEffect(() => {
		fetch(`https://api.digiseller.ru/api/shop/products?seller_id=654077&category_id=${match.params.category}`, {
			headers: {
				Accept: 'application/json',
			},
		})
			.then(response => response.json())
			.then(response => setProducts({ ...response }))
	}, [])

	if (!products) return <>Загрузка списка продуктов...</>
	if (products.retdesc) return <>Такой категории не существует.</>

	return (
		<>
			<p className={styles.pathName}>{products.breadCrumbs[0].name}</p>
			<List component="nav" className={classes.root} aria-label="contacts">
				{products.product.map(prod => (
					<ListItem button key={prod.id} component={Link} to={`/shop/${match.params.category}/${prod.id}`}>
						<ListItemIcon>
							<LocalAtmIcon fontSize="large" style={{ color: '#5bbf97' }} />
						</ListItemIcon>
						<ListItemText
							primary={<span className={styles.listPrimary}>{prod.name}</span>}
							secondary={
								+prod.cntImg ? (
									<span className={styles.listSecondary}>
										Цена: <span className={styles.colored}>{prod.base_price} RUB</span>
									</span>
								) : (
									<span>Нет в наличии.</span>
								)
							}
						/>
					</ListItem>
				))}
			</List>
		</>
	)
}
